
    *{
        margin:0;
        padding:0;
        box-sizing: border-box;
        font-family: 'Montserrat',sans-serif;
    }
    #header{
        position: fixed;
        top:0;
        background: none;
        display:flex;
        color:white;
        justify-content: space-between;
        width:100%;
        padding:29px 64px 0px 46px;
        font-size: 30px;
        a:hover{color:red;}
    }
    .left{
      
        span{font-family: 'Sillii Willinn';
        color:red;
        }
       
    font-weight:400;
    }
    .parallax{
  
        height:100vh; 

        background-attachment: fixed;
        
background-position: center;
background-repeat: no-repeat;
background-size: 100% 100vh;
display: flex;
flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    color: white;
    font-weight: 800;
align-items: center;
justify-content: center;
color:white;


}


.parallax1{
    background-image: url(background/Circus.png);
}

.parallax2{
  
    background-image: url('background/MA.png');
    
}
.parallax3{
    background-image: url('background/Museum.png')
}
.parallax4{

    background-image: url('background/University.png')
}
.parallax5{
    background-image: url('background/Factory.png')
    }
    .parallax6{

background-image: url('background/Subway.png')
}
h1{font-family: 'Montserrat',sans-serif;font-weight: 700;text-align: center;font-size: 100px;}
.view{width:200px;
    height:50px;
    border:2px solid rgb(255 255 255);
    background:none;
    color:white;
    font-family: 'Montserrat',sans-serif;
    font-weight: 600;
    font-size: 16px;
    margin-top: 53px;
border-radius: 2px;
display:flex;
align-items: center;
justify-content: center;
}
.view:hover{
    
background: white;
color:black;
}
a{color: white;
text-decoration: none;}
@media (max-width:1500px) {
.parallax{h1{font-size:50px;}} 
 #header{font-size: 20px;}
}
@media (max-width:1000px) {
    .parallax{h1{font-size:30px;}} 
    #header{font-size: 15px;}
   
    
}
@media(max-width:650px){
    .right{
        padding-top: 30px;
        
        position: absolute;
       top:0;
       left:0;
        flex-direction:column;
     background:black;
     margin-top:-29px;
     
     width:70vw;
     height:120vh;
     
     
     div{text-align: center;padding:30px;} 
    
    }
    .fa{font-size:34px;position: absolute;
    right:20px;}
}